      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
