  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      width: 100%;
    }

    @include r(420) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-weight: normal;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__text {
    p {
      color: rgba(0, 0, 0, 0.6);
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
    }
  }

  &__text-row {
