      margin-bottom: 40px;
    }
  }

  &__text-heading {
    color: #000;
    font-size: 23px;
    line-height: 36px;
    font-weight: normal;
    margin-bottom: 24px;
  }

  &__text-note {
    color: #be0909;
  }

  &__text-description {
