    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

